body {
  font-family: 'Roboto', sans-serif;
}

.custom-btn {
  padding: 20px 30px;
  font-size: 25px;
  font-weight: bold;
  background-color: #3b5998;
  color: #fff;
  border-radius: 50px;
  border: none;
}

.custom-btn:hover {
  background-color: #3b59ff;
}

.btn-light {
  background-color: #fff;
  color: #333;
}

.btn-light:hover {
  background-color: #ddd;
  color: #333;
}

nav {
  background-color: #3b5998;
}

nav.navbar a.nav-link, nav.navbar a.navbar-brand {
  color: #fff;
}

nav.navbar a.navbar-brand {
  font-weight: bold;
}

nav.navbar .navbar-toggler-icon {
  background-color: #fff;
}

section.about-section {
  padding: 150px 0;
  background-color: #eee;
}

h1 {
  font-size: 85px;
  margin-bottom: 20px;
}

p {
  font-style: italic;
  margin-bottom: 50px;
}

section.about-section p {
  color: #333333;
}

section.contact-section {
  padding: 150px 0;
  background-color: #3b5998;
  color: #fff;
}

section.contact-section .form-control {
  padding: 30px 25px;
  font-size: 20px;
}

footer {
  background-color: #eee;
  padding: 50px 0;
}